/**
 * Lists logs for the specified job.
 *
 * Logs are generated by the destination for each item that failed to move. Some destinations also generate logs for items that moved successfully.
 */
export function listMovementLogs(payload: object): RequestOptionsFactory<any> {
  function __listMovementLogs({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.hub.v1.job',
      method: 'GET' as any,
      methodFqn: 'wix.hub.v1.DataMovementService.ListMovementLogs',
      packageName: PACKAGE_NAME,
      url: resolveWixHubV1DataMovementServiceUrl({
        protoPath: '/v1/jobs/{jobId}/logs',
        data: payload,
        host,
      }),
      params: toURLSearchParams(payload),
    };

    return metadata;
  }

  return __listMovementLogs;
}
